html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: helvetica neue, helvetica, arial, sans-serif;
  overflow: hidden;
}

#root {
  --dun: #e8d2b1;
  --platinum: #ebebe8;
  --sandy-brown: #f1a771;
  --flame: #e96838;
  --cambridge-blue: #6ea69d;
  --indigo-dye: #0b3d64;
  height: 100%;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  transition: left .2s ease-in-out;
  display: flex;
  position: relative;
  left: 0;
}

#background {
