}

.sidebar {
  height: 100%;
  width: 200px;
  background-color: var(--indigo-dye);
  color: var(--sandy-brown);
  z-index: 100;
  border-top-right-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin-right: 1em;
  transition: left .2s ease-in-out;
  display: flex;
  position: absolute;
  left: -200px;
  overflow-x: scroll;
}

.pie-holder {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pie-hole {
  border: var(--sandy-brown);
  width: 41%;
  height: 70%;
  cursor: default;
  border-style: solid;
  border-radius: 100%;
  justify-content: end;
  align-items: center;
  display: flex;
}

h1 {
  letter-spacing: 1rem;
  width: 80%;
  text-align: center;
  font-family: sans-serif;
  font-size: 2.5rem;
}

#root.open {
  left: 200px;
}

.sidebar.open {
  transition: left .2s ease-in-out;
  left: 0;
}

.sidebar.close {
  overflow: visible;
}

.sidebar-toggle {
  height: 15px;
  width: 20px;
  z-index: 1;
  cursor: pointer;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  margin: 0 .2rem;
  display: flex;
  position: absolute;
  right: 0;
}

.sidebar-toggle.close {
  color: var(--indigo-dye);
  margin: 0;
  right: -40px;
}

.sidebar-component {
  width: 80%;
  border-style: solid;
  border-color: var(--sandy-brown);
  opacity: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: .5rem;
  padding: 5px 10px;
  display: flex;
  position: relative;
}

.scale-container {
  width: 20px;
  height: 20px;
  background-color: var(--platinum);
  fill: var(--indigo-dye);
  width: 20px;
  height: 20px;
  background-color: var(--platinum);
  fill: var(--indigo-dye);
  border-radius: 10%;
  justify-self: center;
}

.component-button {
  width: 80%;
  letter-spacing: .05rem;
  border: var(--sandy-brown);
  text-align: center;
  cursor: pointer;
  border-style: solid;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  margin: 1px 0;
  padding: .15rem .25rem;
  display: flex;
}

.component-button:active {
  background-color: var(--cambridge-blue);
  color: var(--dun);
}

.sortable {
  border: none;
  border-bottom: var(--dun);
  height: .75rem;
  cursor: default;
  border-bottom-style: solid;
  border-bottom-width: .5px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  display: flex;
}

.disabled {
  cursor: default;
  opacity: .6;
}

.sortable:hover {
  background-color: var(--cambridge-blue);
  color: var(--dun);
}

