  align-self: flex-end;
  align-items: center;
  margin: -3px -5px 2px 0;
  padding: .05rem .2rem;
  font-size: .4rem;
  display: flex;
}

.reset_button:hover {
  background: var(--cambridge-blue);
  color: var(--dun);
}

.reset_button:active {
  background-color: var(--flame);
