  justify-content: space-between;
  align-items: end;
  margin-bottom: 1px;
  font-size: .7rem;
  display: flex;
}

.tooltip-trigger .tooltip-top:after {
  content: " ";
  border-style: solid;
  border-width: 5px;
  border-color: var(--indigo-dye) transparent transparent transparent;
  margin-left: -5px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.tooltip-trigger .tooltip-bottom:after {
  content: " ";
  border-style: solid;
  border-width: 5px;
  border-color: transparent transparent var(--indigo-dye) transparent;
  margin-left: -5px;
  position: absolute;
  bottom: 100%;
  left: 50%;
}

.tooltip-trigger .tooltip-right:after {
  content: " ";
  border-style: solid;
  border-width: 5px;
  border-color: transparent var(--indigo-dye) transparent transparent;
  margin-top: -5px;
  position: absolute;
  top: 50%;
  right: 100%;
}

.tooltip-trigger .tooltip-left:after {
  content: " ";
