  left: 100%;
}

.modal {
  width: 600px;
  max-width: 100%;
  height: 400px;
  max-height: 100%;
  color: var(--indigo-dye);
  background-color: #fff;
  border-radius: .1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 25px;
  display: flex;
  position: fixed;
  top: 50%;
