  color: var(--platinum);
  border: 0;
  border-radius: .5rem;
  padding: 5px 10px;
  font-size: 1.3rem;
  position: absolute;
  top: 10px;
  right: 20px;
}

.modal-close-btn:hover {
  cursor: pointer;
}

.modal-close-btn:active {
  background-color: var(--cambridge-blue);
